.loader {
    border: 4px solid #f3f3f3; /* Цвет рамки */
    border-top: 4px solid #3498db; /* Цвет верхней границы */
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 2s linear infinite; /* Анимация */
    margin: 0 auto;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}